* {
  margin: 0;
  box-sizing: border-box;
}

.menu {
  background: rgb(255, 255, 220, 0.8);
  min-height: 600px;
  font-family: "Courgette", cursive;
  padding-bottom: 20px;
}

.menu-title {
  padding: 32px 0 0 0;
  text-align: center;
  font-size: 28px;
  color: #3f1f01;
}
.menu-buttons {
  margin: 8px 0;
}

.menu-buttons--left,
.menu-buttons--right {
  position: absolute;
  z-index: 100;
  background: transparent;
  border: none;
  font-size: 24px;
  text-align: center;
  color: #30b7c0;
}

.menu-buttons--left {
  left: 35%;
  color: gray;
}

.menu-buttons--right {
  right: 35%;
  color: #30b7c0;
}

/*Desktop Screen*/
@media (min-width: 992px) {
  .menu-product--desktop {
    display: grid;
    max-width: 900px;
    margin: 30px auto;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .menu-title {
    font-size: 32px;
  }
}
