body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  box-sizing: border-box;
}

.sidebar-container {
  background: rgb(255, 255, 240);
  opacity: 0.4;
  padding: 0px;
  height: 100vh;
  width: 100%;
  position: absolute;
  z-index: -5;
  /* top: 0;
  left: 0; */
  transition: 0.8s ease;
}

.sidebar-menu {
  list-style: none;
  display: flex;
  padding: 0px;
  margin: 40px 0;
  flex-direction: column;
  align-items: flex-start;
}

.sidebar-menu li {
  margin: 32px 24px;
  width: 80%;
  text-align: center;
  border-bottom: 0.2px solid #af764c;
}

.sidebar-menu li a {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-size: 22px;
  padding: 6px 32px;
  text-decoration: none;
  color: #af764c;
}

.sidebar-icon--close {
  /* color: #fff; */
  color: #af764c;
  font-size: 34px;
  margin: 11px 0px;
  position: absolute;
  right: 10px;
  top: -54px;
}

* {
  margin: 0;
  box-sizing: border-box;
}

.navbar-container {
  background: rgb(255, 255, 240);
  box-shadow: 0px 6px 8px rgb(236, 235, 235);
  width: 100%;
  height: 50px;
  top: 0;
  position: fixed;
  z-index: 105;
}

.navbar-wrapper {
  display: flex;
  margin: 0 auto;
  padding: 0 16px;
  height: 50px;
  max-width: 1100px;
  justify-content: space-between;
  align-items: center;
}

.sidebar-menu-icon {
  font-size: 24px;
  z-index: 106;
  position: relative;
  top: 4px;
  color: #af764c;
}

.navbar-links {
  display: flex;
  display: none;
  list-style: none;
}

.navbar-link a {
  color: #af764c;
  text-decoration: none;
  margin: 0 16px;
}

.navbar-link a:hover {
  color: #5c3e29;
  transition: color ease 0.5s;
}

.navbar-logo a {
  color: #af764c;
  text-decoration: none;
  font-family: "Courgette", cursive;
  font-size: 24px;
}

/*Tablet screen*/
@media (min-width: 768px) {
  .sidebar-menu-icon {
    display: none;
  }

  .navbar-links {
    display: flex;
  }
}

.footer-container {
  background: rgba(251, 251, 166, 0.8);
}

.footer-logo a {
  display: inline-block;
  text-decoration: none;
  padding: 32px 16px 0 16px;
  font-family: "Courgette", cursive;
  font-size: 24px;
  color: #3f1f01;
}

.footer-wrapper {
  display: flex;
  padding: 32px 16px 60px 16px;
  flex-flow: row wrap;
  justify-content: space-evenly;
}

.footer-wrapper p {
  padding: 8px;
  color: rgba(0, 0, 0, 0.5);
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
}

.copyright {
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5);
  text-align: center;
  padding-bottom: 60px;
}

.copyright p:nth-child(2) {
  margin-top: 4px;
  font-weight: bolder;
  color: black;
}

/*Desktop Screen*/
@media (min-width: 992px) {
  .footer-logo a:hover,
  .footer-wrapper p:hover {
    color: rgba(0, 0, 0);
  }
}

* {
  margin: 0;
  box-sizing: border-box;
}

.hero-section {
  min-height: 500px;
  background: rgb(255, 255, 240);
  font-family: "Courgette", cursive;
}

.hero-container {
  display: flex;
  margin-top: 50px;
  padding: 16px 0 0 0;
  flex-direction: column;
  align-items: center;
}

.hero-content {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.hero-image {
  display: flex;
  flex-direction: column;
}

.hero-image img {
  width: 90%;
  align-self: center;
}

.hero-title {
  padding: 0 12px;
  color: #af764c;
  font-size: 40px;
  max-width: 300px;
  margin: 25px 0;
  text-align: center;
}

.hero-title span {
  color: #3f1f01;
}

.hero-title--info {
  display: none;
}

.hero-button {
  background: rgb(240, 235, 210);
  color: #af764c;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  text-align: center;
  padding: 12px 24px;
  margin: 16px 0;
  width: 80%;
  cursor: pointer;
  border-radius: 10px;
  font-size: 24px;
  margin-bottom: 60px;
}

/*Mobile Landscape screen*/
@media (min-width: 480px) {
  .hero-image img {
    width: 70%;
  }

  .hero-title {
    font-size: 32px;
  }
}

/*Tablet screen*/
@media (min-width: 768px) {
}

/*Desktop Screen*/
@media (min-width: 992px) {
  .hero-container {
    position: relative;
  }

  .hero-content {
    flex-flow: row nowrap;
    align-items: center;
  }

  .hero-image img {
    width: 100%;
  }

  .hero-title {
    font-size: 54px;
    max-width: 400px;
  }

  .hero-title--info {
    display: block;
    font-size: 14px;
    margin: 8px 0 16px 0;
  }

  .hero-button {
    width: 400px;
    padding: 12px 12px;
    font-size: 22px;
    border-radius: 10px;
    position: absolute;
    top: 370px;
    right: 165px;
  }

  .hero-button:hover {
    background: #3f1f01;
    color: #fff;
    transition: all 1s ease;
  }
}

* {
  margin: 0;
  box-sizing: border-box;
}

.menu-item-container {
  padding: 32px 4px;
}

.menu-item-wrapper {
  display: flex;
  background: #30b7c0;
  max-width: 240px;
  border-radius: 38px;
  margin: 0 auto;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.menu-image img {
  width: 240px;
}

.menu-content {
  margin: -4px 0;
  border-radius: inherit;
}

.menu-content p {
  width: 100%;
  overflow: hidden;
  height: 75px;
  text-align: center;
  font-size: 18px;
  color: black;
  padding: 8px 95px;
  overflow-x: hidden;
  background: rgba(255, 255, 255, 0.7);
}

.menu-content a {
  text-decoration: none;
  display: block;
  color: #fff;
  padding: 16px 0;
  font-size: 18px;
  text-align: center;
}

/*Desktop Screen*/
@media (min-width: 992px) {
  .menu-item-container {
    padding: 16px 0;
  }

  .menu-content p {
    overflow: hidden;
  }

  .menu-content a:hover {
    color: black;
  }
}

* {
  margin: 0;
  box-sizing: border-box;
}

.menu {
  background: rgb(255, 255, 220, 0.8);
  min-height: 600px;
  font-family: "Courgette", cursive;
  padding-bottom: 20px;
}

.menu-title {
  padding: 32px 0 0 0;
  text-align: center;
  font-size: 28px;
  color: #3f1f01;
}
.menu-buttons {
  margin: 8px 0;
}

.menu-buttons--left,
.menu-buttons--right {
  position: absolute;
  z-index: 100;
  background: transparent;
  border: none;
  font-size: 24px;
  text-align: center;
  color: #30b7c0;
}

.menu-buttons--left {
  left: 35%;
  color: gray;
}

.menu-buttons--right {
  right: 35%;
  color: #30b7c0;
}

/*Desktop Screen*/
@media (min-width: 992px) {
  .menu-product--desktop {
    display: grid;
    max-width: 900px;
    margin: 30px auto;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .menu-title {
    font-size: 32px;
  }
}

.signup-container {
  background: rgba(255, 255, 220, 0.6);
  padding: 32px 0;
}

.signup-image {
  display: flex;
  justify-content: center;
}

.signup-image img {
  width: 70%;
}

.primary-title {
  font-family: "Courgette", cursive;
  font-size: 26px;
  text-align: center;
  color: #423f6f;
}

.secondary-title {
  margin: 10px 0;
  padding: 0 20px;
  color: rgba(66, 63, 111, 70%);
  text-align: center;
}

.signup-form {
  margin: 40px 0;
  display: flex;
  flex-direction: column;
}

.signup-form-input {
  margin: 0 auto;
  background: #eeeeee;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  width: 70%;
  padding: 16px 24px;
  border-radius: 12px;
  border: none;
  outline: none;
  box-shadow: 2px 7px rgba(0, 0, 0, 9%);
  z-index: 100;
}

.signup-form-button {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  margin: 16px auto;
  width: 70%;
  padding: 16px 24px;
  border-radius: 12px;
  border: none;
  outline: none;
  background: #1faf3f;
  color: #fff;
  box-shadow: 2px 6px rgba(0, 0, 0, 6%);
}

/*Desktop Screen*/
@media (min-width: 992px) {
  .signup-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .signup-cta {
    margin: 0 auto;
  }

  .signup-image img {
    width: 60%;
  }

  .signup-form-input,
  .signup-form-button {
    width: 100%;
  }

  .signup-form-button:hover {
    background: #45d664;
  }
}

.blog-container {
  display: flex;
  flex-flow: column nowrap;
  padding: 32px 0;
  background: rgba(251, 251, 166, 0.35);
}

.first-blog {
  display: flex;
  flex-flow: column nowrap;
}

.blog-image {
  display: flex;
  justify-content: center;
}

.blog-image--image {
  max-width: 75%;
}

.blog-content {
  margin-top: 16px;
  font-family: "Courgette", cursive;
}

.blog-title {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
}

.blog-content--content {
  margin: 12px auto;
  max-width: 75%;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 16px;
}

.blog-content--content span a {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 16px;
  color: #1faf3f;
  text-decoration: none;
}

/*Desktop Screen*/
@media (min-width: 992px) {
  .first-blog {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-evenly;
  }

  .second-blog {
    display: flex;
    flex-flow: row-reverse nowrap;
    align-items: center;
    justify-content: space-evenly;
  }

  .blog-content {
    max-width: 400px;
    text-align: left;
  }

  .blog-title {
    font-size: 26px;
  }

  .blog-content--content {
    font-size: 22px;
  }

  .blog-content--content span a {
    font-size: 22px;
  }

  .blog-content--content span a:hover {
    color: #0f6822;
  }
}

.order-item {
  display: flex;
  position: relative;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  border-bottom: 0.2px solid rgb(0, 0, 0, 0.2);
  padding: 12px 16px 40px 16px;
  width: 90%;
  margin: 0 auto;
}

.item-name {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  padding: 4px 0;
}

.item-price {
  font-size: 14px;
  color: rgb(0, 0, 0, 0.8);
}

.add-button {
  width: 80px;
  text-align: center;
  font-weight: bold;
  padding: 4px 6px;
  margin: 16px 0 0 0;
  border-radius: 7px;
  color: #fff;
  background: #af764c;
  box-shadow: 2px 4px 6px rgb(0, 0, 0, 0.3);
}

.item-image {
  width: 120px;
  height: 80px;
}

.item-image img {
  width: 100%;
  height: 100%;
  border-radius: 7px;
}

.item-choose-quantity {
  display: flex;
  color: green;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
  border: none;
  border-radius: 7px;
  padding: 0.5px 1px;
  position: absolute;
  width: 100px;
  background: #fff;
  top: 80px;
  right: 25px;
  z-index: 10;
  box-shadow: 2px 6px 6px rgb(0, 0, 0, 0.3);
}

/*Tablet screen*/
@media (min-width: 768px) {
  .order-item {
    position: relative;
    flex-flow: column nowrap;
    border-radius: 10px;
    width: 342px;
    box-shadow: 2px 6px 6px rgb(0, 0, 0, 0.3);
  }

  .item-quantity {
    position: absolute;
  }

  .item-image {
    position: relative;
    top: -100px;
    width: 340px;
    height: 220px;
  }

  .item-details {
    position: relative;
    top: 225px;
    left: -70px;
  }

  .item-choose-quantity {
    position: relative;
    top: -60px;
    left: 200px;
  }

  .add-button {
    position: relative;
    z-index: 10;
    width: 100px;
  }
}

/*Mobile Landscape Screen*/
@media (min-width: 480px) {
}

/*Desktop Screen*/
@media (min-width: 992px) {
  .order-item:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: all 0.5s;
  }

  .add-button {
    transition: all 0.8s;
  }
  .add-button:hover {
    background: hsl(25, 39%, 20%);
  }
}

.payment-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding: 0 16px;
  z-index: 20;
}

.payment-success {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.payment-success-message {
  font-size: 40px;
  text-align: center;
  color: #1faf3f;
}

.payment-success-icon {
  color: #1faf3f;
  font-size: 100px;
}

.payment-success-button {
  background: #1faf3f;
  padding: 8px 32px;
  border-radius: 7px;
}

.payment-success-button a {
  font-size: 24px;
  text-decoration: none;
  color: #fff;
}

/*Desktop Screen Size*/
@media (min-width: 992px) {
  .payment-success-button:hover {
    cursor: pointer;
    background: #10a02f;
  }
}

.cart-title {
  text-align: center;
  font-family: "Courgette", cursive;
  color: #af764c;
  margin: 25px auto 0 auto;
  width: 200px;
  border-bottom: 0.5px solid #af764c;
}

.cart {
  display: grid;
  grid-auto-rows: minmax(50px, auto);
  width: 100vw;
  overflow-x: hidden;
  min-height: 500px;
  max-height: 100vh;
  padding: 0 30px;
}

.cart-items,
.cart-total {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-end;
  justify-content: space-evenly;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.2);
}

.cart-items p:nth-child(1) {
  justify-self: left;
  width: 100px;
  font-weight: bold;
}

.cart-total {
  font-weight: bold;
  color: #af764c;
  font-size: 18px;
  align-items: center;
  justify-content: space-around;
  border-bottom: none;
}

.pay-button {
  padding: 4px 24px;
  background: #af764c;
  color: #fff;
  border-radius: 4px;
}

/*Empty Cart*/
.cart-is-empty {
  width: 100%;
  overflow-x: hidden;
  min-height: 500px;
  max-height: 100vh;
}

.cart-is-empty p {
  text-align: center;
  color: #af764c;
  margin: 30px 0 0 0;
  font-size: 30px;
  font-weight: bold;
}

.empty-cart {
  width: 300px;
  margin: 0 auto;
  border-bottom: 0.5px solid #af764c;
}
.empty-cart img {
  width: 100%;
}

.add-items p {
  padding: 8px 12px;
  width: 80%;
  font-weight: normal;
  border-radius: 10px;
  margin: 40px auto;
  text-align: center;
  background: #af764c;
  color: #fff;
}

/*Desktop Screen Size*/
@media (min-width: 992px) {
  .add-items p:hover,
  .pay-button:hover {
    background: #3f1f01;
    color: #fff;
    transition: all 0.5s ease;
  }
}

.cart-bar {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 32px;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 40px;
  background: #af764c;
  z-index: 11;
}

.cart-info {
  display: flex;
  font-weight: bold;
  min-width: 130px;
  align-items: center;
  justify-content: space-around;
  color: #fff;
}

.view-cart {
  color: #fff;
  font-weight: bold;
}

.order-menu-container {
  padding: 60px 0;
}

.order-menu-title {
  margin: 16px 0 4px 0;
  text-align: center;
  font-family: "Courgette", cursive;
  font-size: 24px;
}

/*Tablet screen*/
@media (min-width: 768px) {
  .recommended-items {
    display: grid;
    max-width: 740px;
    margin: 0 auto;
    grid-template-rows: repeat(6, auto);
    grid-template-columns: repeat(2, auto);
    grid-row-gap: 8px;
  }

  .meals-items,
  .snacks-items {
    display: grid;
    max-width: 740px;
    margin: 0 auto;
    grid-template-rows: repeat(4, auto);
    grid-template-columns: repeat(2, auto);
    grid-row-gap: 8px;
  }

  .recommended-items .order-menu-title,
  .snacks-items .order-menu-title,
  .meals-items .order-menu-title {
    margin: 40px auto 20px auto;
    width: 500px;
    border-bottom: 0.7px solid rgba(0, 0, 0, 0.5);
    grid-column-start: 1;
    grid-column-end: span 2;
  }
}

/*Desktop Screen Size*/
@media (min-width: 992px) {
  .recommended-items {
    max-width: 85%;
    grid-gap: 24px;
    grid-template-rows: repeat(5, auto);
    grid-template-columns: repeat(3, auto);
  }

  .meals-items,
  .snacks-items {
    max-width: 85%;
    grid-gap: 24px;
    grid-template-rows: repeat(3, auto);
    grid-template-columns: repeat(3, auto);
  }

  .recommended-items .order-menu-title,
  .snacks-items .order-menu-title,
  .meals-items .order-menu-title {
    grid-column-start: 1;
    grid-column-end: span 3;
  }
}

.about-section {
  display: flex;
  flex-flow: column nowrap;
  padding: 0 40px;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  color: #af764c;
}

.about-section h1 {
  margin: 0 0 20px 0;
}

.about-section p {
  font-size: 24px;
  text-align: center;
}

.about-section span {
  font-family: "Courgette", cursive;
  margin: 16px 0 0 0;
  align-self: flex-end;
}

* {
    margin: 0;
    box-sizing: border-box;
}
.post {
  width: 100vw;
  padding: 60px 40px;
}

.post-title {
  margin: 30px 0;
  font-size: 28px;
  padding: 0 0 4px 0;
  color: #af764c;
  border-bottom: 0.5px solid hsl(25, 25%, 49%);
  font-family: "Courgette", cursive;
  text-align: center;
}

.post-info {
  display: flex;
  justify-content: space-around;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.5);
  align-items: center;
  margin: 30px 0 10px 0;
}

.post-author {
  width: 50px;
  height: 50px;
}

.post-author img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.post-content-photo {
  display: flex;
  margin: 10px 0;
  justify-content: center;
}

.post-content-photo img {
  width: 85%;
}

.post-content--content {
  color: rgba(0, 0, 0, 0.7);
}

.post-author-details {
  display: flex;
  margin: 30px 0 16px 0;
  align-items: center;
}

.post-author-details--photo {
  margin: 0 20px 0 0;
}

.post-author-details--details p:nth-child(1) {
  font-weight: bold;
  color: #5c3e29;
}

.post-author-details--details p:nth-child(2) {
  font-size: 18px;
}

.post-author-details--details p:nth-child(3) {
  font-size: 13px;
}

.post-author-details--details p {
